<script>
  import { LayerCake, Svg, flatten } from "layercake";
  import { timeParse, timeFormat } from "d3-time-format";
  import { scaleTime } from "d3-scale";
  import { format } from "d3-format";

  import Line from "../components/LineSingle.svelte";
  import AxisX from "../components/AxisX.svelte";
  import AxisY from "../components/AxisY.svelte";
  import ScatterSvg from "../components/ScatterSvg.svelte";
  import AxisAnnotation from '../components/AxisAnnotation.svelte';

  export let padding = { top: 16, right: 20, bottom: 20, left: 30 };
  export let data = [];
  export let xField = "date";
  export let yDomain = [0, null];
  export let xDomain = undefined;
  export let xTicks = 3;
  export let yTicks = 4;
  export let formatTickX = timeFormat('%b %-d');
  export let stroke = "";
  export let showAnnotation = false;
  export let isSmallMultiple = false;

  const defFormat = format(',.0f');

</script>
<div class="chart-container {isSmallMultiple ? "small" : ""}">
  <LayerCake
    {padding}
    x={d => d[xField]}
    y={d => +d.value}
    {xDomain}
    yDomain={yDomain}
    data={data}
    flatData={flatten(data)}>
    <Svg>
      <AxisX
        ticks={xTicks}
        gridlines={false}
        snapTicks={false}
        formatTick={formatTickX} />
      <AxisY ticks={yTicks} gridlines={true} formatTick={d => defFormat(d)} />
      <Line stroke={stroke} />
      {#if showAnnotation}
        <AxisAnnotation type="x" label="Deaths" />
        <AxisAnnotation type="y" label="Stringency" yOffset={8} />
      {/if}
    </Svg>
  </LayerCake>
</div>
<style>
  .chart-container{
    width: 100%;
    height: 15em;
  }

  .chart-container.small {
    height: 8em;
  }
</style>