<script>
  import { createEventDispatcher } from 'svelte';
  const dispatch = createEventDispatcher();

  function setSearch() {
    dispatch('message', event.target.value);
  }

  export let fields = [];
  export let searchTerm = fields[0];

  let search = "field-list"

</script>

<label for="searchbar">Days between 10th death date and</label>
<select id="{search}" bind:value={searchTerm} on:change={(evt) => setSearch(evt)} list="{search}">
  {#each fields as d,i}
    <option value="{d}">{d}</option>
  {/each}
</select>

