
<script>
  import { LayerCake, Svg, Html, calcExtents, uniques, flatten } from "layercake";
  import { timeParse, timeFormat } from "d3-time-format";
  import { scaleBand } from "d3-scale";
  import { format } from "d3-format";

  import csvData from '../data/deaths_relative_to_peak.csv';

  import Legend from "../components/Legend.svelte";
  import MultiLine from "../components/MultiLine.svelte";
  import AxisX from "../components/CustomAxisX.svelte";
  import AxisY from "../components/AxisY.svelte";
  import SharedTooltip from '../components/SharedTooltip.svelte';

  export let data = [];
  export let padding = { top: 20, right: 16, bottom: 20, left: 36 };
  export let xTicks = 7;
  export let annotations = [];
  export let withArea = false;

  let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
  const colorScale = {
    "BRA": seriesColors[2],
    "CAN": "#ccc",
    "CHN": seriesColors[4],
    "DEU": "#ccc",
    "ESP": "#ccc",
    "FRA": "#ccc",
    "GBR": seriesColors[0],
    "IND": "#ccc",
    "ITA": seriesColors[3],
    "JPN": "#ccc",
    "KOR": "#ccc",
    "RUS": "#ccc",
    "SWE": "#ccc",
    "USA": seriesColors[1]};
  const parseDate = timeParse('%Y-%m-%d');

  const ctryLookup = {
    BRA: "Brazil",
    CHN: "China",
    GBR: "UK",
    USA: "USA",
    ITA: "Italy"
  }

  const countries = uniques(csvData.map(d => d.CountryCode).filter(d => d !== "ESP"))
  const formattedData = countries.map(e => {
    return { 
      key: e,
      sort: colorScale[e] !== "#ccc" ? 1 : 0,
      values: csvData.filter(d => d.CountryCode === e)
      .map(d => ({
        key: +d.days_since_peak,
        value: +d.indexed_seven_day_average,
      })).filter(d => d.key < 90 && d.key > -90)
      }
  }).sort((a, b) => a.sort - b.sort);

  const tooltipData = csvData.map(d => {
    return {
    key: d.days_since_peak,
    value: d.indexed_seven_day_average,
  }});
</script>

<Legend />
<div class="chart-container">
  <LayerCake
    {padding}
    x={d => d.key}
    y={d => +d.value}
    yDomain={[0, null]}
    xDomain={[-90, 90]}
    data={formattedData}
    flatData={flatten(formattedData.map(d => d.values))}>
    <Svg>
      <AxisX
        gridlines={false}
        overrideMiddle={"Peak"} />
      <AxisY tickNumber={4} gridlines={true} suffix="%" />
      <MultiLine {colorScale} {annotations} />
    </Svg>
    <Html>
      <SharedTooltip
        title={"Percentage of peak"}
        dataset={tooltipData}
        highlight={["BRA","CHN","GBR","ITA","USA"]}
        unit = {"%"}
        formatKey={d => ctryLookup[d]}
      />
    </Html>
  </LayerCake>
</div>

<style>
  .chart-container{
    width: 100%;
    height: 25em;
  }

  @media (max-width: 500px) {
    .chart-container{
      height: 15em;
    }
  }
</style>