<script>
  import { LayerCake, Svg } from "layercake";
  import { timeParse, timeFormat } from "d3-time-format";
  import { scaleBand } from "d3-scale";
  import { format } from "d3-format";

  import MultiLineChart from "./multiline_chart.svelte";

  export let data = [];
  export let order = [];
  export let padding = { top: 20, right: 16, bottom: 20, left: 36 };
  export let yDomain = [0, null];
  export let xTicks = 2;
  export let yTicks = 2;
  export let ticks = null;
  export let withArea = false;

  let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
	const colorLookup = {
    "Brazil": seriesColors[2],
    "CAN": "#8399A8",
    "CHN": seriesColors[4],
    "Germany": "#8399A8",
    "Spain": "#8399A8",
    "France": "#8399A8",
    "UK": seriesColors[0],
    "IND": "#8399A8",
    "Italy": seriesColors[3],
    "JPN": "#8399A8",
    "KOR": "#8399A8",
    "RUS": "#8399A8",
    "Sweden": "#8399A8",
    "US": seriesColors[1]};

  const colorScale = (country) => {
    return {expected_deaths: "#ccc", deaths: colorLookup[country]}
  };
  const parseDate = timeParse('%Y-%m-%d');

/*
  country
    key: keyField
    values: [date, value]
*/

  const keyFields = ["expected_deaths","deaths"];
  const countries = data.map(d => d.country).filter( onlyUnique );
  const countryLabels = data.map(d => d.label ? d.label : d.country).filter( onlyUnique );
  const formattedData = (order.length > 0 ? order : countries).map(e => {
    let buffer = []
    for (let keyField of keyFields){
      buffer.push({
        key: keyField,
        country: e,
        excess_percentage: "(+"+(data.filter(d => d.country === e)[0].excess_percentage-100)+"%)",
        values: data.filter(d => d.country === e)
          .map(d => ({
            date: parseDate(d.date),
            value: d[keyField]
          })
        )}
      )}
    return buffer 
  });

  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }

</script>

  <div class="small-multiple-container">
    {#each (order.length > 0 ? order : countries) as country, i}
      <div class="small-multiple">
        <h5>{(order.length > 0 ? order : countries)[i]}</h5>
        <h5 class="percentage">{formattedData[i][0].excess_percentage}</h5>
        <MultiLineChart
          {padding}
          {xTicks}
          {yTicks}
          yDomain={[0, 80000]}
          colorScale={colorScale(country)}
          {withArea}
          isSmallMultiple={true}
          data={formattedData[i]} />
      </div>
    {/each}
  </div>

<style>
  .small-multiple-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  }

  @media(min-width: 700px) {
    .small-multiple-container {
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }
  }

  .small-multiple {
    box-sizing: border-box;
    margin: 8px;
  }

  h5 {
    display: inline-flex
  }
  .percentage {
    margin-left: 0em;
  }
  
</style>