<script>
  import { LayerCake, Svg, calcExtents, flatten } from "layercake";
  import { timeParse, timeFormat } from "d3-time-format";
  import { scaleBand } from "d3-scale";
  import { format } from "d3-format";
  import { tweened } from 'svelte/motion';
  import * as eases from 'svelte/easing';

  import LineChart from "./line_chart.svelte";

  export let data = []; 
  export let order = [];
  export let padding = { top: 20, right: 16, bottom: 20, left: 36 };
  export let xTicks = 1;
  export let yTicks = 3;
  export let xField = "date";
  export let formatTickX = undefined;
  export let xDomain = "auto";
  export let toggles = false;

  const tweenOptions = {
    duration: 1000,
    easing: eases.cubicInOut
  };

  const parseDate = timeParse('%Y-%m-%d');
  const countries = data.map(d => d.country).filter( onlyUnique );
  const countryLabels = data.map(d => d.title ? d.title : d.country).filter( onlyUnique );
  const toggleOptions = toggles ? [["Deaths ","Cases "],["Per million","Raw number"]] : [];
  let toggleSelection = toggles ? [toggleOptions[0][0].toLowerCase(),toggleOptions[1][0].toLowerCase()] : undefined;

  const extentGetters = [
    { field: 'x', accessor: d => d[xField] },
    { field: 'y', accessor: d => d.value }
  ];

  let formattedData = (order.length > 0 ? order : countries).map(e => {
    const filtered = data.filter(d => d.country === e);
    return {
     label: filtered[0].title ? filtered[0].title : filtered[0].country,
     values: filtered.map(f => ({
        ...f,
        date: parseDate(f.date),
        value: toggleSelection ? +f[toggleSelection[0]+toggleSelection[1]] : +f.value
      }))}
  })

  let fullExtents = calcExtents(flatten(formattedData.map(d => d.values)), extentGetters);

  let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
	const colorScale = {
    "BRA": seriesColors[2],
    "CAN": "#8399A8",
    "CHN": seriesColors[4],
    "DEU": "#8399A8",
    "ESP": "#8399A8",
    "FRA": "#8399A8",
    "GBR": seriesColors[0],
    "IND": "#8399A8",
    "ITA": seriesColors[3],
    "JPN": "#8399A8",
    "KOR": "#8399A8",
    "RUS": "#8399A8",
    "SWE": "#8399A8",
    "USA": seriesColors[1]};

  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }

  function changeToggleSelection(option,index){
    toggleSelection[index] = option.toLowerCase();
  }

  function setTween(selection) {
    tweenedPath.set((order.length > 0 ? order : countries).map(e => {
      const filtered = data.filter(d => d.country === e);
      return {
       label: filtered[0].title ? filtered[0].title : filtered[0].country,
       values: filtered.map(f => ({
          ...f,
          date: parseDate(f.date),
          value: selection ? +f[selection[0]+selection[1]] : +f.value
        }))}
    }))
  }

  function setTweenExtents(fData) {
    tweenedExtent.set(calcExtents(flatten(fData.map(d => d.values)), extentGetters))
  }

  $: tweenedPath = tweened(formattedData, tweenOptions);
  $: tweenedExtent = tweened(fullExtents, tweenOptions);
  $: setTween(toggleSelection);
  $: formattedData2 = (order.length > 0 ? order : countries).map(e => {
    const filtered = data.filter(d => d.country === e);
    return {
     label: filtered[0].title ? filtered[0].title : filtered[0].country,
     values: filtered.map(f => ({
        ...f,
        date: parseDate(f.date),
        value: toggleSelection ? +f[toggleSelection[0]+toggleSelection[1]] : +f.value
      }))}
  })
  $: setTweenExtents(formattedData2);

</script>
  
  {#if toggles}
    <div class="button-container">
      <div class="toggleSelection0">
      {#each toggleOptions[0] as d,i}
        <button
          class="{toggleSelection[0] === d.toLowerCase() ? "selected" : ""}"
          on:click="{() => changeToggleSelection(d,0)}"
        >{d}</button>
        {#if i < toggleOptions[0].length-1}
          <span>/</span>
        {/if}
      {/each}
      </div>
    
      <div class="toggleSelection1">
      {#each toggleOptions[1] as d,i}
        <button
          class="{toggleSelection[1] === d.toLowerCase() ? "selected" : ""}"
          on:click="{() => changeToggleSelection(d,1)}"
        >{d}</button>
        {#if i < toggleOptions[1].length-1}
          <span>/</span>
        {/if}
      {/each}
      </div>
    </div>
  {/if}
  <div class="small-multiple-container">
    {#each (order.length > 0 ? order : countries) as country, i}
      <div class="small-multiple">
        <h5>{$tweenedPath[i].label}</h5>
        <LineChart
          {padding}
          {xTicks}
          {yTicks}
          {xField}
          xDomain={xDomain === "auto" ? fullExtents.x : xDomain}
          {formatTickX}
          yDomain={$tweenedExtent.y}
          stroke={colorScale[country]}
          data={$tweenedPath[i].values}
          isSmallMultiple={true}
          showAnnotation={!toggles && i === 0} />
      </div>
    {/each}
  </div>

<style>

  .small-multiple-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  }

  .small-multiple {
    box-sizing: border-box;
    margin: 8px;
  }

  .button-container {
    display: inline-flex;
  }

  .toggleSelection1 {
    margin-left: 2em;
  }

  .selected{
    background-color: #c1002a;
    color: #fff;
  }
</style>