<script>
  import { LayerCake, Svg, calcExtents } from "layercake";
  import { scaleBand } from "d3-scale";
  import { format } from "d3-format";

  import Bar from '../components/Bar.svelte';
  import AxisX from '../components/CustomAxisX.svelte';
  import AxisY from '../components/AxisY.svelte';
  import TextureLegend from '../components/TextureLegend.svelte'

  export let yDomain = [0, null];
  export let data = [];
  export let yField = "value";
  export let yTitle = "";
  export let ySuffix = "";
  export let yKey = "GBR";
  export let formatTickY = d => d + ySuffix;
  export let labelPrecision;

  const categories = data.map(d => d.category).filter( onlyUnique );
  let chartData = data.map(d => ({
    key: d.country,
    value: +d[yField],
    category: categories.indexOf(d.category)
  })).filter(d => !isNaN(d.value));

  const countries = chartData.map(d => d.key);

  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }

</script>

  <TextureLegend legendNames={categories}/>
  <div class="chart-container">
    <LayerCake
        padding={{ top: 10, right: 10, bottom: 20, left: 40 }}
        y='key'
        x='value'
        yScale={scaleBand().paddingInner([0.33]).round(true)}
        yDomain={countries}
        xDomain={yDomain}
        data={chartData}
      >

      <Svg>
        <AxisX
          ticks={4}
          gridlines={true}
          {yKey}
          overrideMiddle="10th death"
        />
        <AxisY
          gridlines={false}
          formatTick={formatTickY}
        />
        <Bar {labelPrecision} fill="#aedeee" withTexture={true} {yKey}/>
      </Svg>
    </LayerCake>
  </div>

<style>
  .chart-container{
    width: 100%;
    height: 25em;
  }

  @media (max-width: 500px) {
    .chart-container{
      height: 15em;
    }
  }
</style>