<script>
	import { getContext } from 'svelte';

	const { xGet, yGet, data, width, height } = getContext('LayerCake');

  export let annotations = [];
  export let fill;

  annotations = annotations.filter(d => d.value !== "");

  $: getY = (annotation) => {
    const yVal = annotation.yVal; //$data.slice(0).filter(d => d.key === +annotation.value);
    return $yGet({value: yVal}); //yVal.length > 0 ? $yGet(yVal[0]) : $yGet({value: 0});
  }
  
  let colors = ["#E60032","#0063E6","#00E682","#E6C800","#E67800"];

</script>

{#each annotations as annotation, i}
  <circle fill="{fill || colors[i % 5]}" r="4" cx="{$xGet({key: +annotation.value})}" cy="{getY(annotation)}">
    <title>{annotation.title}</title>
  </circle>
{/each}

<style>
  circle {
    stroke: #222;
  }
</style>