<script>
	import { getContext } from 'svelte';

	const { width, height, xScale, yScale, yRange } = getContext('LayerCake');

	export let gridlines = true;
	export let formatTick = d => d;
	export let baseline = false;
	export let snapTicks = false;
	export let xTick = undefined;
	export let yTick = 16;
	export let yKey = "";
	export let dxTick = 0;
	export let dyTick = 0;
	export let overrideMiddle = "";
	export let showTick = 40;

	$: ticks = 6; //$width > 600 ? 6 : 4;
	$: isBandwidth = typeof $xScale.bandwidth === 'function';

	$: tickVals = Array.isArray(ticks) ? ticks :
		isBandwidth ?
			$xScale.domain() :
			$xScale.ticks(ticks);

	function textAnchor(i) {
		if (snapTicks === true) {
			if (i === 0) {
				return 'start';
			}
			if (i === tickVals.length - 1) {
				return 'end';
			}
		}
		return 'middle';
	}
	
	function getTickText(tick, i) {
		if(tick === 0 && overrideMiddle !== "") {
			return overrideMiddle;
		}
		else {
		 return (Math.abs(tick) === showTick ? formatTick(Math.abs(tick)) + " days " + (tick < 0 ? "before" : "after") : "")
		}
	}
</script>

<g class='axis x-axis'>
	{#each tickVals as tick, i}
		<g class='tick tick-{ tick }' transform='translate({$xScale(tick)},{$yRange[0]})'>
			{#if tick === 0 || gridlines === true}
				<line class="{tick === 0 ? "highlight" : ""}" y1='{$height * -1}' y2='0' x1='0' x2='0'></line>
			{/if}
			<text
				x="{xTick || isBandwidth ? $xScale.bandwidth() / 2 : 0 }"
				y='{yTick}'
				dx='{dxTick}'
				dy='{dyTick}'
				fill-opacity='{yKey === tick ? 1 : 0.75}'
				text-anchor='{textAnchor(i)}'>{getTickText(tick, i)}</text>
		</g>
	{/each}
	{#if baseline === true}
		<line class="baseline" y1='{$height + 0.5}' y2='{$height + 0.5}' x1='0' x2='{$width}'></line>
	{/if}
</g>

<style>
	.tick {
		font-size: .725em;
		font-weight: 200;
	}

	line,
	.tick line {
		stroke: #aaa;
		stroke-opacity: 0.5;
	}

	line.highlight {
		stroke-opacity: 1;
	}

	.tick text {
		fill: #222;
	}

	.baseline {
		stroke-dasharray: 0;
	}
</style>
