<script>
  import { LayerCake, Svg, flatten } from "layercake";
  import { timeParse, timeFormat } from "d3-time-format";
  import { scaleBand, scaleOrdinal } from "d3-scale";
  import { format } from "d3-format";

  import ClevelandDotPlot from "../components/ClevelandDotPlot.svelte";
  import AxisX from '../components/AxisX.svelte';
  import AxisY from '../components/AxisY.svelte';
  import Legend from '../components/Legend.svelte';

  export let padding = { top: 16, right: 20, bottom: 20, left: 30 };
  export let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
  export let data = [];
  export let xDomain = [0,null];
  export let xTicks = 3;
  export let yTicks = 4;
  export let formatTick = format(",.0f");
  export let noZeroLine = false;
  export let legend = false;

  const yKey = 'country';
  const yDomain = data.map(d => d[yKey]).filter( onlyUnique ).sort((a, b) => a - b); 
  const xKey = Object.keys(data[0]).filter(d => d !== yKey);

  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }

</script>

{#if legend}
    <Legend {seriesColors} legendNames={xKey}/>
{/if}

<div class="chart-container">
  
  <LayerCake
    {padding}
    x={xKey}
    y={d => d[yKey]}
    yDomain={yDomain}
    yScale={scaleBand().paddingInner([0.05]).round(true)}
    xDomain={xDomain}
    zScale={scaleOrdinal()}
    zDomain={xKey}
    zRange={seriesColors}
    data={data}
  >
    <Svg>
      <AxisX
        ticks={xTicks}
        gridlines={true}
        {noZeroLine} 
        {formatTick} />
      <AxisY ticks={yTicks} gridlines={false}/>
      <ClevelandDotPlot {padding} {seriesColors} />
    </Svg>
  </LayerCake>
</div>

<style>
  .chart-container{
    width: 100%;
    height: 25em;
  }

  @media (max-width: 500px) {
    .chart-container{
      height: 15em;
    }
  }
</style>