<script>
	import { getContext } from 'svelte';

	const { data, xGet, yGet, xScale } = getContext('LayerCake');

	export let r = 5;
	export let fill = '#000';
</script>

{#each $data as datum}
<g class="scatter-group">
	{#each datum.values as d}
		<circle
			cx={$xGet(d) + ($xScale.bandwidth() / 2)}
			cy={$yGet(d)}
			{r}
			fill={d.fill}
		/>
	{/each}
</g>
{/each}
