<script>
	import { getContext } from 'svelte';
	import { scaleOrdinal } from 'd3-scale';

	const { data, xGet, yGet, yScale, config, width } = getContext('LayerCake');

	export let seriesColors;
	export let padding = {};

	$: midHeight = $yScale.bandwidth() / 2;

	$: colorScale = scaleOrdinal()
		.domain($config.x)
		.range(seriesColors);

	function getZerosOut(value, index, self) {
    	return index < 2 ? true : value > self[1] ? true : false;
		}
		
  let colors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
	const countryColor = {
    "BRA": colors[2],
    "CAN": "#8399A8",
    "CHN": colors[4],
    "DEU": "#8399A8",
    "ESP": "#8399A8",
    "FRA": "#8399A8",
    "GBR": colors[0],
    "IND": "#8399A8",
    "ITA": colors[3],
    "JPN": "#8399A8",
    "KOR": "#8399A8",
    "RUS": "#8399A8",
    "SWE": "#8399A8",
		"USA": colors[1]};
</script>

{#each $data as row, i}
		<rect
			x={0}
			width={$width}
			y={$yGet(row)}
			height={$yScale.bandwidth()}
			fill={countryColor[row.country]}
			fill-opacity={0.05}
		/>
{/each}

<g class="dot-group">
	{#each $data as row}
		
		<g>
			<line
				x1="{Math.min(...$xGet(row).filter( getZerosOut ))}"
				y1="{$yGet(row) + midHeight}"
				x2="{Math.max(...$xGet(row).filter( getZerosOut ))}"
				y2="{$yGet(row) + midHeight}"
			></line>

			{#each $xGet(row).filter( getZerosOut ) as circleX, i}
				<circle
					cx="{circleX}"
					cy="{$yGet(row) + midHeight}"
					r="5"
					fill="{colorScale($config.x[i])}"
				/>
			{/each}
		</g>
	{/each}
</g>

<style>
	line {
		stroke-width: 1px;
		stroke: #ccc;
		fill-opacity:0.5;
	}
	circle {
		fill-opacity:0.8;
		stroke: #364b58;
	}
</style>
