<script>
  import { onMount } from 'svelte';

  import { format } from "d3-format";
  import { timeParse, timeFormat } from "d3-time-format";
  import { LayerCake, Svg, Html } from 'layercake';
  import { scaleBand } from 'd3-scale';

  import totalDeath from "./data/Total deaths attributed to Covid-19 per million population (per capital).csv";
  import allData from "./data/Datasheet for NS data project - All.csv";
  import excessDeaths from "./data/Excess deaths.csv"
  import lockdownMeasure from './data/Key lockdown measures.csv';
  import testingMeasure from './data/A testing time.csv';
  import stimulusPackage from './data/Stimulus package.csv';
  import sevenDayAverageDeathsCsv from './data/Seven Day Average Deaths.csv';
  import stringencyCsv from './data/STRINGENCY LINE CHART DATA.csv';
  import chartMeta from './data/chartMeta.csv';
  import creditData from './data/credit-rating.csv';
  import debtContractRelief from './data/Speed and depth of debt or contract relief.csv'

  import GroupedBar from "./graphics/grouped_bar.svelte";
  import PoliticalLine2 from "./graphics/political_line_2.svelte";
  import ColumnChart from "./graphics/column_chart.svelte";
  import TextureColumnChart from "./graphics/texture_column_chart.svelte";
  import SmallMultipleMultiline from './graphics/small_multiple_multiline.svelte';
  import SmallMultipleLine from './graphics/small_multiple.svelte';
  import DumbbellChart from './graphics/dumbbell_chart.svelte';
  import PeakLine from './graphics/peak_line.svelte';
  import PoliticalLine from './graphics/political_line.svelte';
  import ScatterPlot from './graphics/scatter_plot.svelte';

  export let pymChild = {};
  export let styleNs = false; // serif or san-serif;
  export let title = "NSMG-018";

  function updateHeight() {
    setTimeout(() => {
      pymChild.sendHeight()
    }, 50);
  }

  let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
	const colorScale = {
    "BRA": seriesColors[2],
    "CAN": "#8399A8",
    "CHN": seriesColors[4],
    "DEU": "#8399A8",
    "ESP": "#8399A8",
    "FRA": "#8399A8",
    "GBR": seriesColors[0],
    "IND": "#8399A8",
    "ITA": seriesColors[3],
    "JPN": "#8399A8",
    "KOR": "#8399A8",
    "RUS": "#8399A8",
    "SWE": "#8399A8",
    "USA": seriesColors[1]};
  
  const countries = ["BRA","CAN","CHN","DEU","ESP","FRA","GBR","IND","ITA","JPN","KOR","RUS","SWE","USA"];
  const countryNames = ["Brazil","Canada","China","Germany","Spain","France","UK","India","Italy","Japan","Korea","Russia","Sweden","USA"];

  const stringency = stringencyCsv.map(d => ({
    ...d,
    deaths: +d.deaths,
    value: +d.value,
    title: countryNames[countries.indexOf(d.country)],
  }));

  const sevenDayAverageDeaths = sevenDayAverageDeathsCsv.map(d => ({
    ...d,
    title: countryNames[countries.indexOf(d.country)]
  }))

  let sectionData = [
    {
      title: "Deaths and disease",
      isOpen: true,
      summaryTitle: "Total deaths attributed to Covid-19",
      fieldId: 42,
      chartMeta: chartMeta.slice(0,5)
    },
    {
      title: "Lockdown",
      isOpen: true,
      summaryTitle: "Efficacy of lockdown - drop in personal mobility",
      reverse: true,
      fieldId: 61,
      chartMeta: chartMeta.slice(5,8).concat(chartMeta[23])
    },
    {
      title: "Economic",
      isOpen: true,
      summaryTitle: "Forecast GDP change in 2020",
      fieldId: 105,
      isGrid: true,
      chartMeta: [chartMeta[8], chartMeta[9]],
      chartData: [
        {...chartMeta[10], fieldId: 105},
        {...chartMeta[11], fieldId: 107},
        {...chartMeta[12], fieldId: 107, labelPrecision: 0},
        {...chartMeta[13], fieldId: 17}
      ]
    },
    {
      title: "Political",
      isOpen: true,
      summaryTitle: "Approval ratings change",
      chartMeta: [chartMeta[14], chartMeta[15], chartMeta[15]],
      fieldId: 89
    },
    {
      title: "Preparedness",
      isOpen: true,
      isGrid: true,
      chartData: [
        {...chartMeta[16], fieldId: 70 },
        {...chartMeta[17], fieldId: 72 },
        {...chartMeta[18], fieldId: 74 },
        {...chartMeta[19], fieldId: 78 },
        {...chartMeta[20], fieldId: 82 },
        {...chartMeta[21], fieldId: 80 },
        {...chartMeta[22], fieldId: 81 },
       ]
    },
  ];

  let lockdownAnnotations = [
    {fieldId: 53, title: "restrictions being placed on gatherings of 10 people or less."},
    {fieldId: 54, title: "restrictions being placed on internal movement."},
    {fieldId: 55, title: "banning arrivals from some regions."},
    {fieldId: 56, title: "all public events being cancelled."},
    {fieldId: 57, title: "schools being required to close at all levels."},
    {fieldId: 58, title: "people being made to stay home besides exceptions for daily exercise, grocery shopping, and 'essential' trips."},
    {fieldId: 59, title: "public transport being closed or prohibited for most citizens."},
    {fieldId: 60, title: "some sectors being closed or made to work from home."},
  ];

  let peakAnnotation = [
    {fieldId: 49, title: "time to 20% from peak"}
  ]

  let pollingData = [
    {fieldId: 85, title: "First death"},
    {fieldId: 86, title: "500th death"},
    {fieldId: 87, title: "10,000th death"},
    {fieldId: 88, title: "June 25"},
  ]

  lockdownAnnotations = lockdownAnnotations.map(d => {
    return countries.map(e => ({
      country: e,
      title: d.title,
      value: allData.filter(f => +f.id === d.fieldId)[0][e]
  }))});

  peakAnnotation = peakAnnotation.map(d => {
    return countries.map(e => ({
      country: e,
      title: d.title,
      value: allData.filter(f => +f.id === d.fieldId)[0][e]
  }))});

  pollingData = countries.map(e => {
    return {
      key: e,
      sort: colorScale[e] === "#8399A8" ? 0 : 1,
      values: pollingData.map(d => ({
      key: d.title,
      value: +allData.filter(f => +f.id === d.fieldId)[0][e],
      fill: colorScale[e]
  })).filter(d => d.value !== 0 && !isNaN(d.value))}
  }).sort((a, b) => a.sort - b.sort);

  sectionData = sectionData.map(d => ({
    ...d,
    data: !d.fieldId ? undefined : countries.map(e => ({
      country: e,
      y: (d.reverse ? -1 : 1) * allData.filter(f => +f.id === d.fieldId)[0][e].replace(/,/g, "")
    })),
    chartData: d.chartData ? d.chartData.map(g => ({
      ...g,
      data: countries.map(e => ({
        country: e,
        y: (d.reverse ? -1 : 1) * allData.filter(f => +f.id === g.fieldId)[0][e].replace(/,/g, "")
      }))
    })) : undefined,
  }));

  let lockdownEase = countries.map(e => ({
    country: e,
    y: allData.filter(f => +f.id === 108)[0][e].replace(/,/g, "")
  })).filter(d => d.y !== "")

  let creditRating = creditData.map(e => ({
    code: e.country,
    country: countryNames[countries.indexOf(e.country)],
    jan: e.rating_jan,
    may: e.latest_rating,
    outlook: e.outlook,
    diff: e.change_numeric
  }))

	onMount(() => {
    sectionData[0].isOpen = false;
    sectionData[1].isOpen = false;
    sectionData[2].isOpen = false;
    sectionData[3].isOpen = false;
    sectionData[4].isOpen = false;
    setTimeout(() => {
      pymChild.sendHeight()
    }, 50);
  });


</script>

<svelte:head>
  <title>{title}</title>
  <link
  href="https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700&display=swap"
  rel="stylesheet" />
</svelte:head>

<div class="embed-container" class:styleNs>
  <h3>Covid-19 in numbers</h3>
  <p>To better understand how the UK is dealing with the pandemic, the <em>New Statesman</em> has compiled a database containing dozens of key metrics across 14 major nations. You can explore this data in the sections below.</p>
  {#each sectionData as section}
  <details on:click={() => updateHeight()} bind:open={section.isOpen}>
    <summary>
      <div class="grid-container">
        <div class='summary-title {section.title.split(" ")[0]}'><span class="details-chevron">{section.isOpen ? "▾" : "▸"}</span>
        <strong>{section.title}</strong>
          {#if section.title === "Deaths and disease"}
            <p>More than 500,000 people have had their deaths attributed to Covid-19 worldwide to date, but some countries have been impacted far worse than others. Expand this section to see how the UK compares to other major nations on five different mortality measures.</p>{/if}
          {#if section.title === "Lockdown"}
            <p>The true efficacy of lockdowns may not be known for some time, however the UK’s approach to implementing restrictions differs in interesting ways to those imposed by its counterparts. Expand this section to explore these differences and their impacts.</p>
          {/if}
          {#if section.title === "Preparedness"}
            <p>Infrastructure and pre-planning is key to tackling a major crisis such as that of the coronavirus. In this section we explore whether the UK was comparatively well-prepared for the pandemic and whether there were any areas of critical weakness.</p>
          {/if}
          {#if section.title === "Economic"}
            <p>Boris Johnson’s government has pumped an unprecedented level of financial support into the UK in the form of the furlough scheme. But how does this generosity compare internationally, and was it delivered quickly enough? Expand this section to find out.</p>
          {/if}
          {#if section.title === "Political"}
            <p>Public confidence in leadership figures is crucial during a pandemic. If the public don’t trust their leaders then why would they follow their advice? Expand this section to see how trust in different governments have fluctuated during the pandemic.</p>
          {/if}
        <p><em>Click to see charts</em></p>
        </div>
      </div>
    </summary>
    <div class="section {section.isGrid ? "grid" : "" }">
      {#if section.title === "Deaths and disease"}
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[0].title}</h3>
            <h4>{section.chartMeta[0].subtitle}</h4>
            <p>{section.chartMeta[0].text}</p>
          </div>
          <SmallMultipleMultiline order={["UK","Spain","Italy","Sweden","US","France","Brazil","Germany"]} data={excessDeaths} padding={{top: 20, bottom: 20, left: 24, right: 16}} withArea={true}/>
          <div class="source-text">Source: FT analysis of international statistical agencies</div>
        </div>
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[1].title}</h3>
            <h4>{section.chartMeta[1].subtitle}</h4>
            <p>{section.chartMeta[1].text}</p>
          </div>
          <SmallMultipleLine order={["GBR","ITA","SWE","FRA","USA","BRA","CAN","DEU","RUS","IND","JPN","KOR","CHN"]} data={sevenDayAverageDeaths} toggles={true} padding={{top: 20, bottom: 20, left: 24, right: 16}} />
          <div class="source-text">Source: {section.chartMeta[1].source}</div>
        </div>
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[2].title}</h3>
            <h4>{section.chartMeta[2].subtitle}</h4>
            <p>{section.chartMeta[2].text}</p>
          </div>
          <DumbbellChart data={testingMeasure} xDomain={[-1000,25000]} seriesColors={["#ADC3D3","#5B717F","#122834"]} legend={true} />
          <div class="source-text">Source: {section.chartMeta[2].source}</div>
        </div>
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[3].title}</h3>
            <h4>{section.chartMeta[3].subtitle}</h4>
            <p>{section.chartMeta[3].text}</p>
          </div>
          <ColumnChart formatTickY={(d) => format(",.0f")(d)} data={section.data} yTitle={"Total deaths attributed to Covid-19"}/>
          <div class="source-text">Source: {section.chartMeta[3].source}</div>
        </div>
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[4].title}</h3>
            <h4>{section.chartMeta[4].subtitle}</h4>
            <p>{section.chartMeta[4].text}</p>
          </div>
          <PeakLine annotations={peakAnnotation} />
          <div class="source-text">Source: {section.chartMeta[4].source}</div>
        </div>
      {/if}
      {#if section.title === "Lockdown"}
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[0].title}</h3>
            <h4>{section.chartMeta[0].subtitle}</h4>
            <p>{section.chartMeta[0].text}</p>
          </div>
          <SmallMultipleLine order={["GBR","ESP","ITA","SWE","FRA","USA","BRA","CAN","DEU","RUS","IND","JPN","KOR","CHN"]} data={stringency} xField={"deaths"} xDomain={[0,500]} xTicks={1} formatTickX={d => d} padding={{top: 20, bottom: 20, left: 24, right: 16}} />
          <div class="source-text">Source: {section.chartMeta[0].source}</div>
        </div>
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[1].title}</h3>
            <h4>{section.chartMeta[1].subtitle}</h4>
            <p>{section.chartMeta[1].text}</p>
          </div>
          <ColumnChart data={section.data} yDomain={[-100, 0]} />
          <div class="source-text">Source: {section.chartMeta[1].source}</div>
        </div>
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[2].title}</h3>
            <h4>{section.chartMeta[2].subtitle}</h4>
            <p>{section.chartMeta[2].text}</p>
          </div>
          <ScatterPlot data={lockdownMeasure} seriesColors={["#122834"]}/>
          <div class="source-text">Source: {section.chartMeta[2].source}</div>
        </div>
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[3].title}</h3>
            <h4>{section.chartMeta[3].subtitle}</h4>
            <p>{section.chartMeta[3].text}</p>
          </div>
          <ColumnChart data={lockdownEase} rotate={false} />
          <div class="source-text">Source: {section.chartMeta[3].source}</div>
        </div>
      {/if}
      {#if section.title === "Preparedness"}
        {#each section.chartData as chart, i}
        <div class="section-chart {i === 2 ? "wide" : ""}">
          <div class="section-chart-header">
            <h3>{chart.title}</h3>
            <h4>{chart.subtitle}</h4>
            <p>{chart.text}</p>
          </div>
          {#if i !== 2}
          <ColumnChart data={chart.data}/>
          {:else}
          <div class="checklist">
            {#each chart.data as datum}
              <div class="item"><span>{datum.country}:</span><br><span aria-label="{datum.y === 100 ? "has PPE" : "does not have PPE"}" class="check">{datum.y === 100 ? "✅" : "❌"}</span></div>
            {/each}
          </div>
          {/if}
          <div class="source-text">Source: {chart.source}</div>
        </div>
        {/each}
      {/if}
      {#if section.title === "Economic"}
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[0].title}</h3>
            <h4>{section.chartMeta[0].subtitle}</h4>
            <p>{section.chartMeta[0].text}</p>
          </div>
          <table class="credit-table">
            <thead>
              <tr>
                <td>Country</td>
                <td>Jan 2020</td>
                <td>May 2020</td>
                <td>Outlook</td>
              </tr>
            </thead>
            <tbody>
              {#each creditRating as country, i}
              <tr class="{country.code}">
                <td>{country.country}</td>
                <td>{country.jan}</td>
                <td class="{+country.diff === -5 ? "neg" :""}">{country.may}</td>
                <td class="{country.outlook === "Negative" ? "neg" :""}">{country.outlook}</td>
              </tr>
                {/each}
            </tbody>
          </table>
          <div class="source-text">Source: {section.chartMeta[0].source}</div>
        </div>
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[1].title}</h3>
            <h4>{section.chartMeta[1].subtitle}</h4>
            <p>{section.chartMeta[1].text}</p>
          </div>
          <GroupedBar />
          <div class="source-text">Source: {section.chartMeta[1].source}</div>
        </div>
        {#each section.chartData as chart, i}
          <div class="section-chart">
              <div class="section-chart-header">
                <h3>{chart.title}</h3>
                <h4>{chart.subtitle}</h4>
                <p>{chart.text}</p>
              </div>
                {#if i === 2}
                  <TextureColumnChart data={debtContractRelief} yDomain={[-60,50]} labelPrecision={chart.labelPrecision} />
                {:else if i === 3}
                  <ScatterPlot yAxisLabel={"% of GDP"} data={stimulusPackage} seriesColors={["#122834"]} yKey={"Stimulus package as % of GDP"} xKey={"Days between 10th death and first government stimulus measure"}/>
                {:else}
                  <ColumnChart data={chart.data} yDomain={chart.yDomain} />
                {/if}
            <div class="source-text">Source: {chart.source}</div>
          </div>
        {/each}
      {/if}
      {#if section.title === "Political"}
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[0].title}</h3>
            <h4>{section.chartMeta[0].subtitle}</h4>
            <p>{section.chartMeta[0].text}</p>
          </div>
          <PoliticalLine2 data={pollingData} xType="band" />
          <div class="source-text">Source: {section.chartMeta[0].source}</div>
        </div>
        <div class="section-chart">
          <div class="section-chart-header">
            <h3>{section.chartMeta[1].title}</h3>
            <h4>{section.chartMeta[1].subtitle}</h4>
            <p>{section.chartMeta[1].text}</p>
          </div>
          <PoliticalLine />
          <div class="source-text">Source: {section.chartMeta[1].source}</div>
        </div>
      {/if}
      
    </div>
  </details>
  {/each}
</div>

<style>
  @font-face{font-family:"Unit OT";src:url("https://www.citymetric.com/sites/all/themes/creative-responsive-theme/fonts/unitot-webfont.eot?#iefix") format("embedded-opentype"), url("https://www.citymetric.com/sites/all/themes/creative-responsive-theme/fonts/unitot-webfont.woff") format("woff"), url("https://www.citymetric.com/sites/all/themes/creative-responsive-theme/fonts/unitot-webfont.ttf") format("truetype"), url("/sites/all/themes/creative-responsive-theme/fonts/unitot-webfont.svg#unitotregular") format("svg");font-style:normal;font-weight:normal}

  .embed-container {
    font-family: Unit OT, sans-serif;
  }

  em {
    font-weight: normal;
  }

.checklist {
  display: grid;  
  grid-template-columns: repeat(auto-fit, minmax(60px, 1fr));
}

.checklist .item span {
  min-width: 30px;
  display: inline-block;
}

.checklist .item span.check {
  font-size: 2em;
}

.embed-container {
    width: 100%;
    max-width: 900px;
    box-sizing: border-box;
    border-top: 1px solid #222;
    border-bottom: 1px solid #222;
    padding: 8px 0;
  }

  .details-chevron {
    font-size: 2em;
    padding-left: 8px;
    margin-right: 8px;
    position: relative;
    top: 4px;
  }

  details summary { list-style-type: none; }
  summary::-webkit-details-marker {
    display: none;
  }

  details {
    border-top: solid #ccc 2px;
  }

  summary {
    min-height: 8em;
  }

  .grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    width: 100%;
  }
    
  .section {
    width: 100%;
    margin-bottom: 4em;
  }

  .section.grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 2em;
  }

  @media (min-width: 600px) {
    .section-chart.wide {
      grid-column: span 2;
    }
  }

  @media (max-width: 400px) {
    .section.grid {
      display: block;
    }
  }

  .section {
    min-height: 200px;
  }

  .summary-title,
  .summary-chart,
  .section {
    font-weight: initial;
    text-align: start;
  }
  .summary-title { 
    font-family: "Unit OT", sans-serif; /* catch IE not loading fonts */
    font-size: 1em;
    user-select: none; /* supported by Chrome and Opera */
   -webkit-user-select: none; /* Safari */
   -khtml-user-select: none; /* Konqueror HTML */
   -moz-user-select: none; /* Firefox */
   -ms-user-select: none; /* Internet Explorer/Edge */
   padding-right: 25%;
  }

  .summary-title.Deaths {
    background: url('https://nsmediadataunit.github.io/live/nsmg-018/images/deaths.png');
    background-size: 50%;
    background-position: 100% 100px;
    background-repeat: no-repeat;
  }

  .summary-title.Lockdown {
    background: url('https://nsmediadataunit.github.io/live/nsmg-018/images/1.png');
    background-size: 50%;
    background-position: 100% 100px;
    background-repeat: no-repeat;
  }

  .summary-title.Economic {
    background: url('https://nsmediadataunit.github.io/live/nsmg-018/images/2.png');
    background-size: 50%;
    background-position: 100% 50px;
    background-repeat: no-repeat;
  }

  .summary-title.Political {
    background: url('https://nsmediadataunit.github.io/live/nsmg-018/images/3.png');
    background-size: 50%;
    background-position: 100% 100px;
    background-repeat: no-repeat;
  }

  .summary-title.Preparedness {
    background: url('https://nsmediadataunit.github.io/live/nsmg-018/images/4.png');
    background-size: 50%;
    background-position: 100% 100px;
    background-repeat: no-repeat;
  }

  @media (max-width: 600px) {
    .summary-title {
      padding-right: 5% !important;
      background: none !important;
    }
  }

  @media (min-width: 700px) {
    .summary-title {
      background-position: 100% 50px !important;
    }

    .summary-title.Economic {
      background-position: 100% 0 !important;
    }
  }

  .grid .section-chart-header {
    min-height: 10em;
  }
  
  @media (max-width: 600px) {
    .grid .section-chart-header {
      min-height: unset;
    }
  }

  .summary-chart {
    padding: 0.5em 0;
  }

  .cta {
    font-size: 0.75em;
    font-family: "Unit OT", sans-serif; /* catch IE not loading fonts */
  }

  .neg {
    font-weight: bold;
  }

  .credit-table {
    border-collapse: collapse;
    width: 100%;
  }
  .credit-table td {
    border-bottom: solid #ccc 2px;
    padding: 2px;
  }

  .credit-table thead {
    font-weight: bold;
  }

  .credit-table .BRA td {
    border-bottom: solid #E6C800 2px;
  }
  .credit-table .CHN td {
    border-bottom: solid #00E682 2px;
  }

  .credit-table .GBR td {
    border-bottom: solid #E60032 2px;
  }

  .credit-table .USA td {
    border-bottom: solid #0063E6 2px;
  }

  .credit-table .ITA td {
    border-bottom: solid #E67800 2px;
  }


  :global(.chart-container) {
    width: 100%;
    padding: 8px;
    box-sizing: border-box;
  }

  :global(.section-chart) {
    border-top: 1px solid #777;
    margin: 8px 0;
    padding-top: 8px;
    font-family: "Source Sans Pro", sans-serif; /* catch IE not loading fonts */
