<script>
	import { getContext } from 'svelte';

	const { data, xGet, yGet, xScale, yScale } = getContext('LayerCake');

	export let withTexture = false;

	$: columnWidth = d => {
		return Math.abs($xGet({value: 0}) - $xGet(d));
	};

  let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
	const colorScale = {
    "BRA": seriesColors[2],
    "CAN": "#8399A8",
    "CHN": seriesColors[4],
    "DEU": "#8399A8",
    "ESP": "#8399A8",
    "FRA": "#8399A8",
    "GBR": seriesColors[0],
    "IND": "#8399A8",
    "ITA": seriesColors[3],
    "JPN": "#8399A8",
    "KOR": "#8399A8",
    "RUS": "#8399A8",
    "SWE": "#8399A8",
    "USA": seriesColors[1]};
</script>

<g class="bar-group">
	{#each $data as d, i}
		{#if withTexture && d.category > 0}
			<defs>
				<pattern id="boldStripe{i}" width="5" height="5" patternTransform="rotate(135 0 0)" patternUnits="userSpaceOnUse">
			      <rect width="100%" height="100%" fill-opacity=1 fill="white" stroke=none/>
			      <line stroke={colorScale[d.key]} stroke-width="7" stroke-opacity="1" x1="0" y1="0" x2="0" y2="20" />
			    </pattern>
			</defs>
		{/if}
		
		<rect
			class='group-rect'
			data-id="{i}"
			x="{d.value < 0 ? $xGet(d) : $xGet({value: 0})}"
			y="{$yGet(d)}"
			height={$yScale.bandwidth()}
			width="{columnWidth(d)}"
			fill = {!withTexture ? colorScale[d.key] : d.category > 0 ? 'url(#boldStripe'+i+')' : colorScale[d.key]}
		></rect>
	{/each}
</g>