<script>
  import { LayerCake, Html, Svg, uniques, flatten } from 'layercake';
  import { scaleBand } from 'd3-scale';
  import { format } from 'd3-format';

  import csvData from '../data/unemployment.csv';

  import Bar from '../components/GroupedBar.svelte';
  import AxisX from '../components/AxisX.svelte';
  import AxisY from '../components/AxisY.svelte';

  const fields = ["Jan", "Feb", "Mar", "Apr"].reverse();
  const countries = ["BRA","CAN","CHN","DEU","ESP","FRA","GBR","IND","ITA","JPN","KOR","RUS","SWE","USA"];
  const countryNames = ["Brazil","Canada","China","Germany","Spain","France","UK","India","Italy","Japan","Korea","Russia","Sweden","USA"];

  let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
	const colorScale = {
    "BRA": seriesColors[2],
    "CAN": "#8399A8",
    "CHN": seriesColors[4],
    "DEU": "#8399A8",
    "ESP": "#8399A8",
    "FRA": "#8399A8",
    "GBR": seriesColors[0],
    "IND": "#8399A8",
    "ITA": seriesColors[3],
    "JPN": "#8399A8",
    "KOR": "#8399A8",
    "RUS": "#8399A8",
    "SWE": "#8399A8",
    "USA": seriesColors[1]};
  
  let data = [
    countries.map((d, i) => ({
    fill: d,
    key: countryNames[i],
    value: +csvData[0][d]})),
    countries.map((d, i) => ({
    fill: d,
    key: countryNames[i],
    value: +csvData[1][d]})),
    countries.map((d, i) => ({
    fill: d,
    key: countryNames[i],
    value: +csvData[2][d]})),
    countries.map((d, i) => ({
    fill: d,
    key: countryNames[i],
    value: +csvData[3][d]})),
  ];

  const yDomain = countryNames;
</script>

<style>
  .svg-container {
    width: 100%;
    height: 400px;
    min-height: 200px;
    padding: 8px;
  }

  @media (max-width: 600px) {
    .svg-container {
      height: 15em;
    }
  }

  .legend-item {
    display: inline-block;
    background: #8399A8;
    width: 20px;
    height: 10px;
    margin-left: 1em;
  }
</style>

<div class="legend">
  {#each fields.reverse() as field, i}
    <span class="legend-item" style="opacity:{1 / ((fields.length - i) / 2)}"></span> {field} 2020
  {/each}
</div>

<div class="svg-container">
  <LayerCake
    padding={{ top: 16, bottom: 16, left: 52, right: 16 }}
    x='value'
    y='key'
    yScale={scaleBand().paddingInner([0.2]).round(true)}
    {yDomain}
    xDomain={[0, null]}
    {data}
    flatData={flatten(data)}
  > 
    <Svg>
      <AxisX
        ticks={4}
        gridlines={true}
        baseline={true}
        formatTick={d => format(",.0f")(d)}
      />
      <AxisY
        gridlines={false}
        baseline={true}
        formatTick={d => d}
        hours={csvData.map(d => d.time)}
      />
      <Bar {colorScale} />
    </Svg>
  </LayerCake>
</div>
