<script>

	export let legendNames = ["Brazil","China","UK","Italy","USA"];
	export let seriesColors = ["#E6C800","#00E682","#E60032","#E67800","#0063E6"];
	export let inTheChart = undefined;

	if (inTheChart){
		const new_legendNames = inTheChart.filter(d => legendNames.indexOf(d) >= 0);
		const new_seriesColors = new_legendNames.map(d => seriesColors[legendNames.indexOf(d)]);

		legendNames = new_legendNames;
		seriesColors = new_seriesColors;
	}
</script>

<div class='layercake-legends'>
	{#each legendNames as d, i}
		<div class='layercake-legend' data-id='{i}'> 
			<div class='layercake-legend-circle' style='background:{seriesColors[i]}'></div>
			<div class='layercake-legend-label'><span>{d}</span></div>
		</div>
	{/each}
</div>

<style>
	.layercake-legend {
		display: inline-block;
	    vertical-align: top;
	    margin-right: 2em;
	    white-space: nowrap;
		
	}
	.layercake-legend-circle {
		display: inline-block;
	    vertical-align: middle;
	    width: 1em;
	    height: 1em;
	    border-radius: 100%;
	}
	.layercake-legend-label {
		font-size: 12px;
		display: inline-block;
    	vertical-align: middle;
	}
</style>