<script>
  import { LayerCake, Svg, flatten } from "layercake";
  import { timeParse, timeFormat } from "d3-time-format";
  import { scaleBand } from "d3-scale";
  import { format } from "d3-format";

  import Dot from "../components/ScatterSvgScaleBand.svelte";
  import MultiLine from "../components/MultiLine.svelte";
  import AxisX from "../components/AxisX.svelte";
  import AxisY from "../components/AxisY.svelte";

  export let padding = { top: 16, right: 20, bottom: 20, left: 30 };
  export let colorScale;
  export let data = [];
  export let yDomain = [0, null];
  export let xDomain = undefined;
  export let xField = "date";
  export let xTicks = 3;
  export let xLines = false;
  export let yTicks = 4;
  export let formatTickX = timeFormat('%b %-d');
  export let height = "";
  export let annotations = [];

  const defFormat = format(',.0f');

</script>
<div class="chart-container {height}">
  <LayerCake
    {padding}
    x={d => d[xField]}
    y={d => +d.value}
    yDomain={yDomain}
    xDomain={xDomain}
    xScale={scaleBand()}
    data={data}
    flatData={flatten(data.map(d=> d.values))}>
    <Svg>
      <AxisX
        ticks={xTicks}
        gridlines={xLines}
        formatTick={formatTickX} />
      <AxisY gridlines={true} formatTick={d => defFormat(d)} />
      <MultiLine {colorScale} {annotations} opactiy={0.5} />
      <Dot {colorScale} {annotations} />
    </Svg>
  </LayerCake>
</div>

<style>
  .chart-container{
    width: 100%;
    height: 15em;
  }

  .tall {
    height: 25em;
  }

  @media (max-width: 500px) {
    .chart-container{
      height: 15em;
    }
  }
</style>