<script>
	import { getContext } from 'svelte';

	const { data, xGet, yGet, xScale, yScale, extents } = getContext('LayerCake');

	export let colorScale = d => '#000';

	$: paths = $data.map(d => {
		return 'M' + d.values
			.map(d => {
				return $xGet(d) + ',' + $yGet(d);
			})
			.join('L');
	})

	$: yRange = $yScale.range();
	$: areas = [
		paths[0] + ( 
			'L' + $xScale($extents.x[1]) + ',' + yRange[0] +
			'L' + $xScale($extents.x[0]) + ',' + yRange[0] +
			'Z'),
		paths[0] + (
			'L' + $xScale($extents.x[1]) + ',' + yRange[1] +
			'L' + $xScale($extents.x[0]) + ',' + yRange[1] +
			'Z')
	];
	$: clipArea = [
		paths[1] + ( 
			'L' + $xScale($extents.x[1]) + ',' + yRange[1] +
			'L' + $xScale($extents.x[0]) + ',' + yRange[1] +
			'Z'),
		paths[1] + (
			'L' + $xScale($extents.x[1]) + ',' + yRange[0] +
			'L' + $xScale($extents.x[0]) + ',' + yRange[0] +
			'Z')
	];

</script>

<g class="area-group">
	{#each $data as group,i}
		<clipPath id='{group.country}-{group.key}-clip'>
			<path class='path-area'  d='{clipArea[i]}'></path>
		</clipPath>
		<path class='path-area' clip-path='url(#{group.country}-{group.key}-clip)' d='{areas[i]}' fill='{colorScale[group.key]}' fill-opacity="0.2"></path> 	
	{/each}
</g>
	
