<script>
  import { getContext } from 'svelte';
  import { tweened } from 'svelte/motion';
  import * as eases from 'svelte/easing';

  const { data, xGet, yGet, xScale, yScale } = getContext('LayerCake');

  export let r = 5;
  export let fill = '#000';
  export let stroke = '#0cf';
  export let strokeWidth = 0;
  export let dx = 0;
  export let dy = 0;
  export let selectedKey = "";


  let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
	const colorScale = {
    "BRA": seriesColors[2],
    "CAN": "#8399A8",
    "CHN": seriesColors[4],
    "DEU": "#8399A8",
    "ESP": "#8399A8",
    "FRA": "#8399A8",
    "GBR": seriesColors[0],
    "IND": "#8399A8",
    "ITA": seriesColors[3],
    "JPN": "#8399A8",
    "KOR": "#8399A8",
    "RUS": "#8399A8",
    "SWE": "#8399A8",
    "USA": seriesColors[1]};

  let chartData = $data.map(d => ({
      ...d,
      value: d[selectedKey] === "null" ? 0 : +d[selectedKey],
      isHidden: d[selectedKey] === "null" ? 0 : 1
  }));

</script>

<g class="scatter-group">
  {#each chartData as d,i}
    {#if d !== undefined}
      <circle
        cx={$xGet(d) + (typeof dx === 'function' ? dx($xScale) : dx)}
        cy={$yGet(d) + (typeof dy === 'function' ? dy($yScale) : dy)}
        {r}
        fill={colorScale[d.country]}
        {stroke}
        fill-opacity={d.isHidden}
        stroke-opacity={d.isHidden}
        stroke-width={strokeWidth}
      />
    {/if}
  {/each}
</g>