<script>
  import { LayerCake, Svg, flatten } from "layercake";
  import { timeParse, timeFormat } from "d3-time-format";
  import { format } from "d3-format";

  import MultiLine from "../components/MultiLine.svelte";
  import Area from "../components/AreaInBetween.svelte";
  import AxisX from "../components/AxisX.svelte";
  import AxisY from "../components/AxisY.svelte";

  export let padding = { top: 16, right: 20, bottom: 20, left: 30 };
  export let colorScale;
  export let data = [];
  export let yDomain = [0, null];
  export let xField = "date";
  export let xTicks = 2;
  export let xLines = false;
  export let yTicks = 3;
  export let formatTickX = timeFormat('%b %-d');
  export let height = "";
  export let annotations = [];
  export let withArea = false;
  export let isSmallMultiple = false;

  const defFormat = format(',.0f');

</script>
<div class="chart-container {height} {isSmallMultiple ? "small" : ""}">
  <LayerCake
    {padding}
    x={d => d[xField]}
    y={d => +d.value}
    yDomain={yDomain}
    data={data}
    flatData={flatten(data.map(d => d.values))}>
    <Svg>
      {#if withArea}
        <Area {colorScale} />
      {/if}
      <AxisX
        ticks={[timeParse("%d/%m/%y")("26/01/20"), timeParse("%d/%m/%y")("20/05/20")]}
        gridlines={xLines}
        snapLast={true}
        formatTick={formatTickX} />
      <AxisY ticks={yTicks} gridlines={true} formatTick={d => defFormat(d)} />
      <MultiLine {colorScale} {annotations} />
    </Svg>
  </LayerCake>
</div>

<style>
  .chart-container{
    width: 100%;
    height: 15em;
  }

  .tall {
    height: 25em;
  }


  @media (max-width: 500px) {
    .chart-container{
      height: 15em;
    }
  }

  .chart-container.small {
    height: 8em;
  }
</style>