<script>
	import { getContext } from 'svelte';

	const { data, xGet, yGet, xScale, yScale } = getContext('LayerCake');
	
  export let colorScale = ["#00bbff"];
  const reversed_data = $data.reverse();
</script>

{#each reversed_data as e, i}
<g class="bar-group">
	{#each e as d, j}
		<rect
			class='group-rect'
			data-id="{j}"
			x="{$xScale.range()[0]}"
			y="{$yGet(d) + ($yScale.bandwidth() / reversed_data.length) * i}"
			height={$yScale.bandwidth() / reversed_data.length}
			width="{$xGet(d)}"
      fill={colorScale[d.fill]}
			fill-opacity={1 / ((reversed_data.length-i) / 2)}
		></rect>
	{#if d.annotation}
		<text
			x="{$xScale.range()[0] + 2}"
			y="{$yGet(d) + ($yScale.bandwidth() / reversed_data.length) * 2 - 4}"
		>
		{d.annotation}
		</text>
	{/if}
	{/each}
</g>
{/each}

<style>
	text {
		font-size: 0.725rem;
		fill: #222;
	}
</style>