<script>
  import { getContext } from 'svelte';
  import { LayerCake, Svg, calcExtents } from 'layercake';
  import { scaleBand } from "d3-scale";
  import { format } from "d3-format";
  import { tweened } from 'svelte/motion';
  import * as eases from 'svelte/easing';

  import AxisAnnotation from '../components/AxisAnnotation.svelte';
  import AxisX from "../components/CustomAxisX.svelte";
  import AxisY from '../components/AxisY.svelte';
  import ScatterLollipop from '../components/ScatterLollipop.svelte';
  import ScatterSvg from '../components/ScatterSvg.svelte';
  import Legend from '../components/Legend.svelte';
  import OptionBar from './optionBar.svelte';

  export let data = [];
  export let padding = { top: 16, right: 20, bottom: 20, left: 30 };
  export let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
  export let xTicks = 6;
  export let yTicks = 4;
  export let formatTick = format(",.0f");
  export let yKey = 'country';
  export let xKey = Object.keys(data[0]).filter(d => d !== yKey);
  export let yAxisLabel = "";

  const mode = typeof(xKey) !== "string" ? "lollipop" : "scatter"
  const yDomain = mode === "lollipop" ? data.map(d => d[yKey]).filter( onlyUnique ).sort((a, b) => a - b) : [0,50];
  const yScale = mode === "lollipop" ? scaleBand().paddingInner([0.05]).round(true) : undefined;
  const xDomain = mode === "lollipop" ? [-75,75] : [-25, 25];

  let searchTerm = mode === "lollipop" ? xKey : xKey[0];
  const tweenOptions = {
    duration: 1000,
    easing: eases.cubicInOut
  };

  function filterMessage(event) {
    searchTerm = event.detail;
  }
  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }

  $: searchTerm = mode === "lollipop" ? xKey[0] : xKey;
  $: chartData = data.map(d => ({
    ...d,
    value: +d[searchTerm]
  })).filter(d => !isNaN(d.value))

</script>

{#if mode === "lollipop"}
  <OptionBar 
    fields={xKey}
    {searchTerm}
    on:message={filterMessage}
  />
{:else}
  <Legend />
{/if}

<div class="chart-container">

  <LayerCake
    {padding}
    x={'value'}
    y={d => d[yKey]}
    yDomain={yDomain}
    yScale={yScale}
    xDomain={xDomain}
    data={chartData} >
    <Svg>
      <AxisX
        ticks={xTicks}
        gridlines={true}
        overrideMiddle={"10th death"}
        showTick={mode === "lollipop" ? 60 : 20}
        {formatTick} />
      <AxisY ticks={yTicks} gridlines={mode === "lollipop" ? false : true} />
      {#if yAxisLabel !== ""}<AxisAnnotation type="y" label={yAxisLabel} />{/if}
      {#if mode === "lollipop"}
        <ScatterLollipop selectedKey={searchTerm} />
      {:else}
        <ScatterSvg selectedKey={searchTerm}  />
      {/if}
    </Svg>

  </LayerCake>
</div>

<style>
  .chart-container{
    width: 100%;
    height: 25em;
  }

  @media (max-width: 500px) {
    .chart-container{
      height: 15em;
    }
  }
</style>