<script>
  import { LayerCake, Svg, calcExtents } from "layercake";
  import { scaleBand } from "d3-scale";
  import { format } from "d3-format";

  import Column from '../components/Column.svelte';
  import AxisX from '../components/CountryAxisX.svelte';
  import AxisY from '../components/AxisY.svelte';

  export let yDomain = [0, null];
  export let data = [];
  export let yField = "y";
  export let yTitle = "";
  export let ySuffix = "";
  export let yKey = "GBR";
  export let formatTickY = d => d + ySuffix;
  export let rotate = true;

  let chartData = data.map(d => ({
    key: d.country,
    value: +d[yField],
  })).sort((a, b) => a.value - b.value);

  const countries = chartData.map(d => d.key);

</script>

  <div class="chart-container">
    <LayerCake
        padding={{ top: 30, right: 0, bottom: 30, left: 40 }}
        x='key'
        y='value'
        xScale={scaleBand().paddingInner([0.33]).round(true)}
        xDomain={countries}
        {yDomain}
        data={chartData}
      >

      <Svg>
        <AxisX
          gridlines={false}
          {yKey}
          {rotate}
        />
        <AxisY
          ticks={2}
          gridlines={true}
          formatTick={formatTickY}
        />
        <Column fill="#aedeee" {yKey}/>
      </Svg>
    </LayerCake>
  </div>

<style>
  .chart-container{
    width: 100%;
    height: 12em;
  }
</style>