<script>
  import { LayerCake, Svg, calcExtents, uniques } from "layercake";
  import { timeParse, timeFormat } from "d3-time-format";
  import { scaleBand } from "d3-scale";
  import { format } from "d3-format";

  import csvData from '../data/yougov-chart.csv';

  import Legend from "../components/Legend.svelte";
  import MultiLineChart from "./multiline_chart.svelte";

  export let data = [];
  export let padding = { top: 20, right: 16, bottom: 20, left: 36 };
  export let xTicks = 5;
  export let ticks = null;
  export let annotations = [];


  let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
  const colorScale = {
    "BRA": seriesColors[2],
    "Canada": "#ccc",
    "CHN": seriesColors[4],
    "Germany": "#ccc",
    "Spain": "#ccc",
    "France": "#ccc",
    "UK": seriesColors[0],
    "India": "#ccc",
    "Italy": seriesColors[3],
    "Japan": "#ccc",
    "KOR": "#ccc",
    "RUS": "#ccc",
    "Sweden": "#ccc",
    "USA": seriesColors[1]};
  const parseDate = timeParse('%Y-%m-%d');

  const countries = ["Canada","France","Germany","India","Italy","Japan","Spain","Sweden","UK","USA"]; //uniques(csvData.map(d => d.CountryCode))
  const formattedData = countries.map(e => {
    return { 
      key: e, 
      values: csvData.map(d => ({
        key: timeParse("%Y-%m-%d")(d["\ufeffDateTime"]),
        value: +d[e],
      })).filter(d => d.value !== 0)
      }
  });
</script>

<Legend inTheChart={countries}/>
<MultiLineChart
  {padding}
  {xTicks}
  xField={"key"}
  formatTickX={d => timeFormat("%b %d")(d)}
  yDomain={[0, null]}
  {colorScale}
  height="tall"
  data={formattedData}
  {annotations} />