<script>
	import { getContext } from 'svelte';
	import { format } from 'd3-format';

	const { data, xGet, yGet, width, height, xScale } = getContext('LayerCake');

	$: columnHeight = d => {
		return Math.abs($yGet({value: 0}) - $yGet(d));
	};

	/* --------------------------------------------
	 * Default styles
	 */
	export let fill = '#00e047';
	export let keyFill = '#18a1cd';
	export let yKey = '';
	export let stroke = '';
	export let strokeWidth = 0;
	export let withTexture = false;
	export let labelPrecision;

  let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
	const colorScale = {
    "BRA": seriesColors[2],
    "CAN": "#8399A8",
    "CHN": seriesColors[4],
    "DEU": "#8399A8",
    "ESP": "#8399A8",
    "FRA": "#8399A8",
    "GBR": seriesColors[0],
    "IND": "#8399A8",
    "ITA": seriesColors[3],
    "JPN": "#8399A8",
    "KOR": "#8399A8",
    "RUS": "#8399A8",
    "SWE": "#8399A8",
    "USA": seriesColors[1]};

</script>


<g class="column-group">
	{#each $data as d, i}
		
		{#if withTexture && d.category > 0}
			<defs>
				<pattern id="boldStripe{i}" width="5" height="5" patternTransform="rotate(135 0 0)" patternUnits="userSpaceOnUse">
			      <rect width="100%" height="100%" fill-opacity=1 fill="white" stroke=none/>
			      <line stroke={colorScale[d.key]} stroke-width="7" stroke-opacity="1" x1="0" y1="0" x2="0" y2="20" />
			    </pattern>
			</defs>
		{/if}
		
		<rect
			class='group-rect'
			data-id="{i}"
			x="{$xGet(d)}"
			y="{d.value > 0 ? $yGet(d) : $yGet({value: 0})}"
			width={$xScale.bandwidth()}
			height="{columnHeight(d)}"
			fill = {!withTexture ? colorScale[d.key] : d.category > 0 ? 'url(#boldStripe'+i+')' : colorScale[d.key]}
			fill-opacity = "0.8"
			stroke="{stroke}"
			stroke-width="{strokeWidth}"
		></rect>
		{#if d.key == yKey}
			<text 
				x="{$xGet(d)+$xScale.bandwidth()/2}"
				y="{d.value > 0 ? $yGet(d)-5 : $yGet({value: 0}) - 5}"
			>{format(`,.${labelPrecision !== undefined ? labelPrecision : d.value > 10 ? 0 : 1}f`)(d.value)}</text>
		{/if}
	{/each}
</g>

<style>
	.column-group {
		font-size: .725em;
		font-weight: bold;
	}

	.column-group text {
		fill: #666;
		text-anchor: middle;
	}
</style>