<script>
	import { getContext } from 'svelte';
	import LineAnnotation from '../graphics/line_annotation.svelte';

	const { data, xGet, yGet, xScale } = getContext('LayerCake');

	export let colorScale = d => '#000';
	export let annotations = [];
	export let opactiy = 1;

	$: xOffset = $xScale.bandwidth ? $xScale.bandwidth() / 2 : 0;

	$: getAnnotation = (group) => {
		let annotation = annotations[0].filter(d => d.country === group.key)[0];
		if(annotation) {
			let chartVal = group.values.filter(d => d.key === +annotation.value)[0];

			return [
				{...annotation,
				yVal: +chartVal.value}
			];
		}
		return [];
	}

	$: path = values => {
		return 'M' + values
			.map(d => {
				return $xGet(d) + xOffset + ',' + $yGet(d);
			})
			.join('L');
	};

</script>

<g class="line-group">
	{#each $data as group}
		<path
			class='path-line'
			d='{path(group.values)}'
			stroke="{colorScale[group.key]}"
			stroke-opacity={opactiy}
		></path>
		{#if annotations.length > 0}
			<LineAnnotation
				annotations={getAnnotation(group)}
				fill="{colorScale[group.key]}"
			/>
		{/if}
	{/each}
</g>

<style>
	.path-line {
		fill: none;
		stroke-linejoin: round;
		stroke-linecap: round;
		stroke-width: 2px;
	}
</style>
